import { getUserInfo, getUserSubmissions, getUserRatingHistory } from '@/lib/api';
import { calculateYearStats } from '@/lib/stats';
import ReportView from '@/components/ReportView';

type Props = {
  params: Promise<{ handle: string }>;
};

export default async function ReportPage({ params }: Props) {
  const { handle } = await params;
  
  try {
    // Parallel data fetching
    const [userInfo, submissions, ratingHistory] = await Promise.all([
      getUserInfo(handle),
      getUserSubmissions(handle),
      getUserRatingHistory(handle)
    ]);

    const stats = calculateYearStats(submissions, ratingHistory, 2025); // Assuming 2025

    return <ReportView user={userInfo} stats={stats} />;
  } catch (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-slate-950 text-white">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-red-500 mb-4">Error</h1>
          <p className="text-slate-400">{(error as Error).message}</p>
          <a href="/" className="mt-8 inline-block text-blue-400 hover:underline">Try again</a>
        </div>
      </div>
    );
  }
}
