import axios from 'axios';
import { CFSubmission, CFUser, CFRatingChange } from '@/types/cf';

const CF_API_BASE = 'https://codeforces.com/api';

export async function getUserInfo(handle: string): Promise<CFUser> {
  const response = await axios.get(`${CF_API_BASE}/user.info`, {
    params: { handles: handle }
  });
  if (response.data.status !== 'OK') {
    throw new Error(`Failed to fetch user info: ${response.data.comment}`);
  }
  return response.data.result[0];
}

export async function getUserSubmissions(handle: string): Promise<CFSubmission[]> {
  // Fetching all submissions. For a real app, you might want to paginate or limit.
  const response = await axios.get(`${CF_API_BASE}/user.status`, {
    params: { handle }
  });
  if (response.data.status !== 'OK') {
    throw new Error(`Failed to fetch submissions: ${response.data.comment}`);
  }
  return response.data.result;
}

export async function getUserRatingHistory(handle: string): Promise<CFRatingChange[]> {
  const response = await axios.get(`${CF_API_BASE}/user.rating`, {
    params: { handle }
  });
  if (response.data.status !== 'OK') {
    throw new Error(`Failed to fetch rating history: ${response.data.comment}`);
  }
  return response.data.result;
}
